	module.exports = {"data":{"markdownRemark":{"html":"<h2>Prime Numbers</h2>\n<p>A prime number is any number greater than 1 that can only be divided evenly by 1 or itself. If it can be divided by another number, then it is a composite number.</p>\n<p>Examples:</p>\n<p>5 is a prime number because it can only be divided evenly by itself(5), and 1.</p>\n<p>12 is a not a prime number as it can be divided evenly by 1, 2, 3, 4, 6, and 12.</p>\n<p>The first ten prime numbers are: 2, 3, 5, 7, 11, 13, 17, 19, 23, and 29.</p>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<!-- Please add any articles you think might be helpful to read before writing the article -->","fields":{"slug":"/mathematics/prime-numbers/"},"frontmatter":{"title":"Prime Numbers"}}},"pathContext":{"slug":"/mathematics/prime-numbers/"}}

